.ant-breadcrumb{
    width: 100%;
    overflow-x: hidden;
    overflow: auto !important;
    background: #ccc;
    .ant-breadcrumb-link{
        position: relative;
        a{
            display: inline-block;
            padding: 11px 25px;
            cursor: pointer;
        }
        .active{
            background: #fff;
        }
        i{
            position: absolute;
            right: 0;
            top: -10px;
            font-size: 12px;
            cursor: pointer;
        }
    }
}