.statistic-box {
  width: 100%;
  height: 100%;
}

.statistic-box ul {
  width: 100%;
  height: 50px;
  border: 1px solid #dbdee2;
  margin: 10px 0;
}

.statistic-box ul li {
  width: 150px;
  height: 100%;
  float: left;
  text-align: center;
  line-height: 50px;
  list-style: none;
  color: #f08137;
}

.statistic-box ul li:nth-child(1) {
  width: 200px;
  background: #f08137;
  color: #fff;
}

.statistic-box .content {
  width: 1200px;
  margin: 0 auto;
}

.statistic-box .content .top {
  width: 100%;
  height: 50px;
  line-height: 50px;
  padding-left: 15px;
  border-bottom: 2px solid #F17B31;
  font-size: 22px;
  font-weight: 600;
}

.statistic-box .content .shop-box {
  width: 100%;
}

.statistic-box .content .shop-box .shop-item {
  width: 19%;
  max-width: 250px;
  border: 1px solid #dbdee2;
  float: left;
  margin: 6px;
}

.statistic-box .content .shop-box .shop-item .img-box {
  width: 100%;
  height: 160px;
}

.statistic-box .content .shop-box .shop-item .img-box > img {
  width: 100%;
  height: 100%;
}

.statistic-box .content .shop-box .shop-item .bank-img {
  width: 100%;
}

.statistic-box .content .shop-box .shop-item .bank-img > img {
  width: 100%;
}

.statistic-box .content .shop-box .shop-item .con {
  width: 100%;
  height: 60px;
}

.statistic-box .content .shop-box .shop-item .con > p {
  width: 100%;
  margin: 0;
  padding-left: 15px;
}

.statistic-box .content .shop-box .shop-item .con > p > b {
  display: inline-block;
  color: red;
  font-size: 20px;
}
